.Button {
  padding: 4px;
  background: #fff;
  border-radius: 23px;
  box-shadow: inset 0px -3.94118px 3.94118px rgba(255, 193, 93, 0.26);
  font:  400 33px/51px Baloo;
  letter-spacing: 0em;
  text-shadow: 0.656862735748291px 0.656862735748291px 1.313725471496582px rgba(0, 0, 0, 0.16) inset;

  text-align: center;
}

.Button:hover, .Button_item:hover {
  cursor: pointer;
}

.Button_item {
  padding: 10px 55px;
  background: #FF9E00;
  box-shadow: inset 0px 2.62745px 5.2549px #FFD5AE, inset 0px -2.62745px 6.56863px rgba(170, 61, 0, 0.39);
  border-radius: 20px;
  color: rgba(249, 249, 250, 1);
}

.Button a {
  text-decoration: none;
}
